<template>
  <div class="MonHocToan">
    <raw-loader
      :mytxt="require('!raw-loader!./UploadBasic4.vue').default"
    ></raw-loader>
    <h1>Upload file page</h1>
    <a href="https://www.itsolutionstuff.com/post/file-upload-using-vue-js-axios-phpexample.html" target="_blank">
      https://www.itsolutionstuff.com/post/file-upload-using-vue-js-axios-phpexample.html
    </a>
    <b-row class="mt-2 border-bottom mb-3">
      <b-col md="1" v-for="x in ds" :key="x">
        <div class="border mb-2 position-relative bg-dark text-light" style="height:50px;">
          <img
            width="100%"
            height="50px"
            :src="`/api/FileUpload/GetImage/${x}`"
            alt="Image 1"
          />
          <span class="position-absolute" style="right:1px;">one</span>
          <span class="position-absolute" style="bottom:1px;right:1px;">
            <button @click="onDel(x)">Del</button>
          </span>
          <span class="position-absolute" style="bottom:1px;left:1px;">
          </span>
          <span class="position-absolute" style="top:1px;left:1px;">four</span>
        </div>
      </b-col>
    </b-row>

    <div class="row">
      <div class="col-sm-4 border-right">
        <div>{{listInfo}}</div>
        <form enctype="multipart/form-data" @submit.stop.prevent="onUpload">
          <img id="output" ref="output" width="200" height="110" class="mb-1" />
          <input type="file" id="file" ref="file" class="m-1" @change="onFileChange" /><br>
          <div v-if=" files!=null && files.length> 0 " >
            <button type="submit" class="mr-1">Upload</button>
            <button @click="onRemove" class="mr-1">Remove</button>
          </div>
        </form>
      </div>

      <div class="col-sm-6 border-right bg-primary" >
        <ul>
          <li v-for="x in ds" :key="x">
            {{ x }}
            <button @click="onView(x)">View img</button>
          </li>
        </ul>
      </div>
      <div class="col-sm-2">
        <div>{{ds[0]}}</div>        
        <b-img thumbnail :src="src" alt="Image 1"></b-img>
      </div>
    </div>
  </div>
</template>
<script>
import $ from "jquery";
import axios from "axios";
import moment from "moment";
export default {
  name: "Tpl",
  components: {},
  data() {
    return {
      files:null,
      listInfo:[],
      ds: [],
      src: "",
    };
  },
  mounted() {
    let that = this;
    this.getListImg().then((res) => {
      this.ds = res.data;
    });
  },
  methods: {
    getListInfo(files){
      let ar=[];
      for(var i=0;i<files.length;i++){
        let info={
          name:files[i].name,
          lastModified:files[i].lastModified,
          size:files[i].size,
          type:files[i].type
        }
        ar.push(info);
      }
      return ar;
    },
    onFileChange(){
      var supportedFormats = ['image/jpg','image/gif','image/png','image/jpeg'];
      let files=this.$refs.file.files;
      let index=supportedFormats.findIndex( x => x==files[0].type );
      if(index >= 0){
        alert('file hợp lệ');
      }else{
        alert('file khong hop le');
      }
      this.files=this.$refs.file.files;
      this.listInfo=this.getListInfo(this.$refs.file.files);
      this.getReadAsDataURL(this.files[0]).then((res) => {
        $(this.$refs.output).attr("src", res);
      });
    },      
    onDel(x){
      this.Del(x).then(res=>{
        this.loadListImg();
      })
    },
    onView(x) {
      this.ReadImage(x).then((res) => {
        this.src = `data:image/jpg;base64,${res.data}`;
      });
    },
    getReadAsDataURL(file) {
      return new Promise((resolve, reject) => {
        let reader = new FileReader();
        reader.onload = () => {
          resolve(reader.result);
        };
        reader.onerror = reject;
        reader.readAsDataURL(file);
      });
    },
    onRemove(e){
      e.preventDefault();
      $(this.$refs.file).val("");
      this.files=null;
      $(this.$refs.output).attr('src','');
    },
    onUpload() {
      if (window.FormData !== undefined) {
        var fileUpload = $("#file").get(0); //$("#FileUpload1").get(0).files
        var files = fileUpload.files;
        if(files.length==0){
          alert('thieu file');
          return;
        }
        var formData = new FormData();
        formData.append("myfile", files[0]);//<--- Phải trùng với tham số api
        formData.append("ten","thuc 101");//<--- Phải trùng với tham số api
        let that = this;
        $.ajax({
          url: "/api/FileUpload/Upload4", ///Home/UploadFiles
          type: "POST",
          contentType: false, // Not to set any content header
          processData: false, // Not to process data
          data: formData,
          success: function (result) {
            that.loadListImg();
            that.files=null;
            $("#output").attr("src", '');
          },
          error: function (err) {
            alert(err.statusText);
          },
        });
      } else {
        alert("FormData is not supported.");
      }
    },
    loadListImg() {
      this.getListImg().then((res) => {
        this.ds = res.data;
      });
    },
    async getListImg() {
      return await axios.get(`/api/FileUpload/GetListFile`).catch((err) => {
        console.log(err);
      });
    },
    async ReadImage(fileName) {
      return await axios
        .get(`/api/FileUpload/ReadImage/${fileName}`)
        .catch((err) => {
          console.log(err);
        });
    },
    async GetImage(fileName) {//asDataUrl
      return await axios
        .get(`/api/FileUpload/GetImage/${fileName}`)
        .catch((err) => {
          console.log(err);
        });
    },
    async ReadImage(fileName) {//asByte
      return await axios
        .get(`/api/FileUpload/ReadImage/${fileName}`)
        .catch((err) => {
          console.log(err);
        });
    },
    async Del(fileName) {
      return await axios
        .get(`/api/FileUpload/Del/${fileName}`)
        .catch((err) => {
          console.log(err);
        });
    },
  },
};
</script>
